// App.js
import React from "react";
import "./Users.css";
import axios from "axios"; // uimport axios
import { useEffect } from 'react';
import { useNavigate } from "react-router-dom";

// 6020b0f6bc844d479d3b1cb3290cf72e
axios.defaults.withCredentials = true;

//   export default true ?
  
//   function Users(props){

//     return (
//       <h1>This is My Users Page</h1>
//     )
// } :
function Home(props){
	const navigate = useNavigate();
		  useEffect(() =>{
			  if(!localStorage.getItem('token')){
				navigate("/");
			  }
			})
  }


class Users extends React.Component {
	
	// Constructor
	constructor(props) {
		super(props);
		this.state = {
			items: [],
			DataisLoaded: false,
		};
	}
	// ComponentDidMount is used to
	// execute the code
	componentDidMount() {
		axios.get(
				"http://127.0.0.1:8000/snaccu/getUsers/1", {headers: {
					"Authorization": `token ${localStorage.getItem('token')}`
				  }}
			)
			.then((res) => res.data)
			.then((json) => {
				this.setState({
					items: json,
					DataisLoaded: true,
				});
			});
	}
	render() {
		const { DataisLoaded, items } = this.state;
		if (!DataisLoaded)
			return (
				<div>
					<h1> Pleses wait some time.... </h1>
				</div>
			);

		return (
			<div className="App">
			{/* <h1 className="geeks">Accuknox</h1>
			<h3>Users Deatils</h3> */}
			<div className="container">
				{items && items.map((item) => (
					<ol key={item.id}>
					<div className="item">
							<div>
								<strong>
									{"User_Name: "}
								</strong>
								{item.email},
							</div>
							<div>
								Full_Name: {item.first_name+" "+item.last_name},
							</div>
							<div>
								User_Email: {item.email}
							</div>
					</div>
					</ol>
				))}
			</div>
		</div>
		);
	}
}

export default localStorage.getItem('token') ? Users : Home;
