import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { useNavigate } from "react-router-dom";
import Cookies from "universal-cookie";
import './Login.css';
const cookies = new Cookies();




const Login = (props) => {
  // const [email, setEmail] = useState('');
  // const [password, setPassword] = useState('');
  const navigate = useNavigate();
  const [formData, setFormData] = useState({
    email: '',
    password: '',
});
  const [errors, setErrors] = useState({});

  useEffect(() =>{
    if(localStorage.getItem('user-info')){
      navigate("/");
    }
  })

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({
        ...formData,
        [name]: value,
    });
};

  const handleSubmit = async (event) => {
    event.preventDefault();
    const newErrors = validateForm(formData);
    setErrors(newErrors);

    if (Object.keys(newErrors).length === 0) {
      // Form submission logic here
      try {
        const response = await axios.post('http://127.0.0.1:8000/snaccu/loginUser', {
          headers: {
                      'X-Csrftoken': cookies.get("csrftoken")
                    },
          ...formData
        });
        if(response.data["Message"]==="Login Success"){
          console.log('Login Successful:', response.data);
          props.showAlert("Login Success", "Success");
          localStorage.setItem('user-info', JSON.stringify(response.data.UserInfo));
          localStorage.setItem('token', response.data.UserInfo.token);
        }
        else{
          props.showAlert("Login Error", "Fail");
        }
        
      navigate("/");
      } catch (error) {
        props.showAlert("Login Error", error);
        console.error('Login Error:', error);
      }
      console.log('Form submitted successfully!');
  } else {
      console.log('Form submission failed due to validation errors.');
  }
  };

  const validateForm = (data) => {
    const errors = {};

    if (!data.email.trim()) {
        errors.email = 'Email is required';
    } else if (!/\S+@\S+\.\S+/.test(data.email)) {
        errors.email = 'Email is invalid';
    }

    if (!data.password) {
        errors.password = 'Password is required';
    }

    return errors;
};

  return (
    <div className="form-container">
            <h2 className="form-title">Login Here</h2>
            <form onSubmit={handleSubmit}>
                <div>
                    <label className="form-label">
                        Email:
                    </label>
                    <input
                        className="form-input"
                        type="email"
                        name="email"
                        value={formData.email}
                        onChange={handleChange}
                    />
                    {errors.email && (
                        <span className="error-message">
                            {errors.email}
                        </span>
                    )}
                </div>
                <div>
                    <label className="form-label">
                        Password:
                    </label>
                    <input
                        className="form-input"
                        type="password"
                        name="password"
                        value={formData.password}
                        onChange={handleChange}
                    />
                    {errors.password && (
                        <span className="error-message">
                            {errors.password}
                        </span>
                    )}
                </div>
                <button className="submit-button" type="submit">Submit</button>
            </form>
        </div>
  );
};

export default Login;