import React from "react";
import { Link, useNavigate } from "react-router-dom";

export default function Navbar(props){
  const navigate = useNavigate();

  const Logout = async => {
        localStorage.clear()
        
        navigate("/login");
  }

    return (
        <nav className="navbar navbar-expand-lg bg-body-tertiary">
  <div className="container-fluid">
    <Link className="navbar-brand" to="/">{props.title}</Link>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
      <ul className="navbar-nav me-auto mb-2 mb-lg-0">
        <li className="nav-item">
          <Link className="nav-link active" aria-current="page" to="/">Home</Link>
        </li>
        <li className="nav-item">
          <Link className="nav-link active" aria-current="page" to="/blog">Blog</Link>
        </li>
        <li className="nav-item">
          <Link className="nav-link active" aria-current="page" to="/contact">Contact</Link>
        </li>
        <li className="nav-item">
          <Link className="nav-link active" aria-current="page" to="/about">About</Link>
        </li>
        <li className="nav-item">
        {localStorage.getItem('user-info')?<Link className="nav-link active" aria-current="page" to="/users">Users</Link>:""}
        </li>
      </ul>
      <form className="d-flex" role="search">
        <input className="form-control me-2" type="search" placeholder="Search" aria-label="Search"/>
        <button className="btn btn-outline-success" type="submit">Search</button>
      </form>
      {!localStorage.getItem('user-info')?
      <Link className="btn btn-outline-success mx-2" type="submit" to="/login">Login</Link>:
      <button className="btn btn-outline-success mx-2" type="submit" onClickCapture={Logout}>Logout</button>}
    </div>
  </div>
</nav>
    )
}